}

.header-heading {
  width: 100%;
  font-size: 32px;
  font-family: 'Yu Gothic', 'Arial Narrow', Arial, sans-serif;
  font-weight: 500;
  letter-spacing: 0.4rem;
  text-align: center;
}

/* Position and sizing of burger button */
.bm-burger-button {
position: relative;
margin: 0 auto;
width: 36px;
height: 30px;
right: -40%;
top: -30px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
background: #a90000;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
height: 24px;
width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  top: 53px;
  height: unset;
  max-height: 600px;
}

.mobile-header {
  right: 0;
  width: 70%;
}

/* General sidebar styles */
.bm-menu {
background: rgba(0,32,45, 0.9);
padding: 16px 64px;
font-size: 1.15em;
overflow: unset;
overflow-y: scroll;
}

.mobile-menu {
  overflow-y: hidden;
  left: 0;
  display: flex;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
color: #FFFFFF;
height: 60px;
}

/* Individual item */
.bm-item {
display: inline-block;
text-decoration: none;
font-size: 20px;
line-height: 150%;
color: white;
font-weight: 700;
display: block;
padding: 0.5em 0 0.25em 0;
}

/* Styling of overlay */
.bm-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  left: 0;
  top: 0;
}


